import eventBus from './eventbus.js'

const DelteModal = {
    props: ['tb'],
    template: `<div class="pop-window fade" id="modal-del">
                <div class="alert">
                    <div class="alert_con">
                        <i class="material-icons red">error_outline</i>
                        <p>{{ment}}</p>
                        <p>{{ment2}}</p>
                    </div>
                    <div class="modal_foot">
                        <span  class="b_red"></span>
                        <span v-on:click='ModalClose' class="b_sgrey"></span>
                    </div>
                </div>
            </div>`,
    data() {
        return {
            idx: null,
            thisTarget: null,
            Data: null,
            ment: '  ?',
            ment2:'(  )'
        }
    },
    created() {
        eventBus.$on('News', (Data) => {
            this.ment = ' '
            console.log(Data)
        })
        eventBus.$on('consul', (Data) => {
            this.ment = ' '
            console.log('' + Data)
        })
        eventBus.$on('NewsImg', (Data) => {
            this.ment = '  '
            console.log('' + Data)
        })
        eventBus.$on('account_del', (Data) => {
            this.ment = '  '

            console.log(' ' + Data)
        })
        eventBus.$on('account_use', (Data) => {
            this.ment = '   '
            this.ment2 = ''

            console.log('  ' + Data)
        })
    },
    methods: {
        ModalClose() {
            const Modal = document.getElementById('modal-del')
            Modal.style.opacity = '0';

            setTimeout(() => {
                Modal.style.display = 'none';
            }, 100);
        }

    }
}

export default DelteModal;