import SaveModal from '../../glc/save-modal.js';
import DelteModal from '../../glc/del-modal.js';
import eventBus from '../../glc/eventbus.js';

const NewsView = {
    props: ['id'],
    template: `
    <div class="con_wrap">
        <save-modal></save-modal>
        <delte-modal></delte-modal>
        <div class='content new_bord'>
            <h2 v-if = "id ==='new'">  </h2>
            <h2 v-else> </h2>
            <div class="panel mody">
            <ul>
                <li><h5></h5></li>
                <li class='harf'>
                    <input type='text' v-if="id === 'new' || list.writer === ''"/>
                    <input type='text' v-else v-bind:value='list.writer'/>
                </li>
                <li><h5></h5></li>
                <li class='harf'>
                    <select v-if="id === 'new' || list.cate === ''">
                        <option value=''></option>
                        <option value=''></option>
                        <option value=''></option>
                        <option value=''></option>
                        <option value=''></option>
                    </select>
                    <select v-else>
                        <option v-if="list.cate === ''" value='' selected></option>
                        <option v-else value=''></option>

                        <option v-if="list.cate === ''" value='' selected></option>
                        <option v-else value=''></option>

                        <option v-if="list.cate === ''" value='' selected></option>
                        <option v-else value=''></option>

                        <option v-if="list.cate === ''" value='' selected></option>
                        <option v-else value=''></option>

                        <option v-if="list.cate === ''" value='' selected></option>
                        <option v-else value=''></option>

                    </select>
                </li>
                <li><h5></h5></li>
                <li>
                    <input type='text' v-if="id === 'new'" value=''/>
                    <input type='text' v-else v-bind:value='list.title'/>
                </li>
                <li><h5></h5></li>
                <li>
                    <input type='text' v-if="id === 'new'" value=''/>
                    <input type='text' v-else v-bind:value='list.link'/>
                </li>
                <li><h5></h5></li>
                <li>
                <div class="input-file" v-if="id ==='new' || list.img === ''">
                    <input type="text" readonly="readonly" class="file-name" /> 
                    <label for="upload01" class="file-label"> </label> 
                    <input type="file" name="" id="upload01" class="file-upload" ref="mainimg" />
                </div>

                <div class="input-file" v-else>
                    <a href='list.img' target='blank'>{{list.img}}</a>
                    <label for="upload01" class="file-label b_red" v-on:click="OpenDelteModal(list.idx,'NewsImg')">
                         
                    </label>
                </div>
                </li>
                <li><h5></h5></li>
                <li>
                    <textarea>... </textarea>
                </li>
            </ul>
            </div>

            <div class="btn_wrap">
                <span class="b_red" v-if="id != 'new'" v-on:click="OpenDelteModal(list.idx,'News')"></span>
                <span class="b_blue" v-if="id === 'new'" v-on:click="OpenSaveModal('new','new')"></span>
                <span class="b_blue" v-else v-on:click="OpenSaveModal(list.idx,'updateNews')"></span>
                <router-link tag='span' to='/newsbord' class="b_sgrey"></span>
            </div>

        </div>
    </div>`,
    components:{
        'save-modal':SaveModal,
        'delte-modal':DelteModal
    },
    data() {
        return {
            list: {
                idx: 0,
                writer:"",
                img: "images/dev_img.png",
                link:'http://123213',
                title: '        ',
                join: 340,
                cate: "",
                desc:""
            }

        }
    },
    created() {
        this.fileUploderStyle()

    },
    methods: {
        fileUploderStyle() {
            (function ($) {
                var $fileBox = null;
                $(function () {
                    init();
                })

                function init() {
                    $fileBox = $('.input-file');
                    fileLoad();
                }

                function fileLoad() {
                    $.each($fileBox,
                        function (ids) {
                            var $this = $fileBox.eq(ids),
                                $btnUpload = $this.find('[type="file"]'),
                                $label = $this.find('.file-label');
                            $btnUpload.on('change', function () {
                                var $target = $(this),
                                    fileName = $target.val(),
                                    $fileText = $target.siblings('.file-name');
                                $fileText.val(fileName);
                            })
                            $btnUpload.on('focusin focusout',
                                function (e) {
                                    e.type == 'focusin' ? $label.addClass('file-focus') : $label.removeClass('file-focus');
                                })
                        })
                }
            })(jQuery);
        },
        OpenSaveModal(Data,mode) {
            const Modal = document.getElementById('modal-alert')
            Modal.style.display = 'block';
            setTimeout(() => {
                Modal.style.opacity = '1';
            }, 100);
            eventBus.$emit(mode,Data)
        },

        OpenDelteModal(Data,mode) {
            const Modal = document.getElementById('modal-del')
            Modal.style.display = 'block';
            setTimeout(() => {
                Modal.style.opacity = '1';
            }, 100);
            eventBus.$emit(mode,Data)
        }
    }
}

export default NewsView;